import { Injectable } from '@angular/core';
import { IFormBuilderEvent } from '@helix/platform/shared/api';
import { Subject } from 'rxjs';
import { IFormBuilderEditor } from './form-builder-editor.interface';

@Injectable()
export class FormBuilderService {
  private editorEventSubject = new Subject<IFormBuilderEvent>();
  private focusEditorSubject = new Subject<IFormBuilderEditor>();

  editorEvent$ = this.editorEventSubject.asObservable();
  focusEditor$ = this.focusEditorSubject.asObservable();

  setFocusEditor(focusEditor: IFormBuilderEditor) {
    this.focusEditorSubject.next(focusEditor);
  }

  dispatch(builderEvent: IFormBuilderEvent): void {
    this.editorEventSubject.next(builderEvent);
  }
}
